import { createStore } from "vuex";
import hasher from "./crypting";
import axios from "axios";

export default createStore({
  state: {
    server: "",
    working: {
      BOT: true,
      Syriatel: true,
      MTN: true,
      Adsl: true,
    },
  },
  mutations: {
    setLink: (state, password) => {
      state.server = `https://sato-fill.herokuapp.com/${hasher(password)}`;
    },
    setWorkingServices: async (state) => {
      let getWorking = await axios.get(`${state.server}/services`);
      state.working = getWorking.data;
    },
  },
  actions: {},
  modules: {},
});

