const crypto = require("crypto");
const hasher = (text) => {
  const hash = crypto.createHash("sha256");
  let returnedValue;

  hash.on("readable", () => {
    const data = hash.read();
    if (data) {
      returnedValue = data.toString("hex");
    }
  });

  hash.write(text);
  hash.end();
  return returnedValue;
};

module.exports = hasher;
