<template>
  <s-header>
    <template v-slot:logo>
      <span>SATOFILL - Control Panel</span>
    </template>
    <template v-slot:password>
      <div>
        <input type="password" v-model="password" id="password" />
      </div>
    </template>
    <template v-slot:nav>
      <div>
        <router-link to="/">Home</router-link>
        <router-link to="/charges">Charges</router-link>
        <router-link to="/payments">Payments</router-link>
        <router-link to="/adsl">Adsl</router-link>
        <router-link to="/controls">Controls</router-link>
      </div>
    </template>
    <template v-slot:info>
      <div>users: {{ users }} | points: {{ points }}</div>
    </template>
  </s-header>
  <router-view />
</template>

<script>
import axios from "axios";
import SHeader from "./components/SHeader.vue";
export default {
  name: "app",
  components: { SHeader },
  data() {
    return {
      password: "",
      users: 0,
      points: 0,
    };
  },
  watch: {
    password(newPassword) {
      if (newPassword.length === 10) {
        this.$store.commit("setLink", newPassword);
        this.getUsers();
        this.$store.commit("setWorkingServices");
      }
    },
  },
  methods: {
    async getUsers() {
      let users = await axios.get(`${this.$store.state.server}/allusers`);
      this.users = users.data.users;
      this.points = users.data.points;
    },
  },
};
</script>

