<template>
  <div class="home">
    <section>
      <h3>Get the cash for one user</h3>
      <input type="text" id="get_target" placeholder="user id" />
      <button @click="getCash()">get cash</button>
      <p>{{ getCashResult ? `This user's cash is ${getCashResult}` : "" }}</p>
    </section>
    <section>
      <h3>Update the cash for one user</h3>
      <input type="text" id="update_target" placeholder="user id" />
      <input type="number" id="update_cash" placeholder="new cash" />
      <button @click="updateCash()">update cash</button>
    </section>
    <section>
      <h3>Update the hold for one user</h3>
      <input type="text" id="update_hold_target" placeholder="user id" />
      <input type="number" id="update_hold" placeholder="new hold" />
      <button @click="updateHold()">update hold</button>
    </section>
    <section>
      <h3>Add/Remove from a user</h3>
      <input type="text" id="edit_target" placeholder="user id" />
      <input type="number" id="edit_cash" placeholder="amount" />
      <button @click="editCash('add')">Add</button>
      <button @click="editCash('remove')">Remove</button>
    </section>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from "axios";
export default {
  name: "Home",
  data() {
    return {
      getCashResult: "",
    };
  },
  methods: {
    async getCash() {
      let target = document.getElementById("get_target").value;
      try {
        let res = await axios.get(`${this.$store.state.server}/cash/${target}`);
        this.getCashResult = res.data.cash;
        alert(res.statusText);
      } catch (err) {
        alert(
          "SOMETHING WENT WRONG PLEASE TRY AGAIN, AND CHECK YOUR CONNECTION OR PASSWORD"
        );
      }
    },
    async updateHold() {
      let target = document.getElementById("update_hold_target").value;
      try {
        let res = await axios.put(
          `${this.$store.state.server}/hold/${target}`,
          {
            hold: document.getElementById("update_hold").value,
          }
        );
        alert(res.statusText);
      } catch (err) {
        alert(
          "SOMETHING WENT WRONG PLEASE TRY AGAIN, AND CHECK YOUR CONNECTION OR PASSWORD"
        );
      }
    },
    async updateCash() {
      let target = document.getElementById("update_target").value;
      try {
        let res = await axios.put(
          `${this.$store.state.server}/hold/${target}`,
          {
            cash: document.getElementById("update_cash").value,
          }
        );
        alert(res.statusText);
      } catch (err) {
        alert(
          "SOMETHING WENT WRONG PLEASE TRY AGAIN, AND CHECK YOUR CONNECTION OR PASSWORD"
        );
      }
    },
    async editCash(op) {
      let target = document.getElementById("edit_target").value;
      try {
        let res = await axios.put(
          `${this.$store.state.server}/cash/${op}/${target}`,
          {
            amount: document.getElementById("edit_cash").value,
          }
        );
        alert(res.statusText);
      } catch (err) {
        alert(
          "SOMETHING WENT WRONG PLEASE TRY AGAIN, AND CHECK YOUR CONNECTION OR PASSWORD"
        );
      }
    },
  },
};
</script>
